<template>
  <div class="addresslist-box">
    <header class="flex-row">
           <i class="iconfont cl_333">&#xe682;</i>
           <h3 class="f38 cl_333"></h3>
           <div class="right-btn cl_f90 f28" @click="add()"></div>
    </header>
    <ul class="addresslist mt30">
        <li class="address-item flex-row" v-for="(item,index) in list" :key="index">
            <i class="iconfont">&#xe6e4;</i>
            <div class="text">
                <p><span class="name f34 mr12">{{item.name}}</span><span class="tel cl_999 f24">{{item.tel}}</span> 
                   <span v-if="item.isDefault==true" class="defaultR"></span>
                </p>
                <p class="address mt25">{{item.provice}}{{item.address}}</p>
            </div>
            <div class="right-edit" @click="edit(item)"></div>
        </li>
    </ul>
  </div>
</template>
<style>
   header{
      height: 0.92rem;
      background-color: #ffffff;
      align-items: center;
      padding:0 0.3rem;
      box-sizing: border-box;
   }
   header h3{
     font-weight: normal;
   }
   .address-item{
      height: 2.11rem;
      background-color: #ffffff;
      padding:0.3rem;
      box-sizing: border-box;
      width:100%;
      margin-bottom:0.24rem;
      align-items: center;
   }
   .defaultR{
      display: inline-block;
      color: #f90;
      background-color: #fffbf3;
      border: solid 1px #ff9900;
      padding: 0 0.15rem;
      height: 0.4rem;
      line-height: 0.38rem;
      box-sizing: border-box;
      border-radius: 4px;
   }
    .address-item .text{
        width:70%;
    }
    .address-item .address{
       width:100%;
       color: #333;
       font-size: 0.28rem;
    }
    .address-item .right-edit{
        width: 1rem;
        color: #666;
        font-size: 0.28rem;
        height: 0.8rem;
        border-left: 1px solid #d1d3d8;
        line-height: 0.8rem;
        padding-left: 0.3rem; 
    }
    .mr12{margin-right:0.12rem}
</style>
<script>
export default {
  name:'.addresslist-box',
  data() {
    return {
      chosenAddressId: '1',
      list: [
        {
          id: '1',
          name: '',
          tel: '13000000000',
          provice:'',
          address: ' 138  7  501 ',
          isDefault: false,
        },
        {
          id: '2',
          name: '',
          tel: '1310000000',
          provice:'',
          address: ' 50 ',
          isDefault: false,
        }
      ],
    };
  },
  methods: {
     add(){
         //
         this.$router.push('/address/edit');
     },
     edit(item){
        this.$router.push({
           name:'edit',
           params:{
             item:item
           }
        })
     }
  },
  created(){
     console.log(this.$store.state.vuex.Daddress);
     console.log(this.$route.params.id);
     let daddress = this.$store.state.vuex.Daddress;
     this.list.unshift(daddress);
     for(let i =0;i<this.list.length;i++){
          let element = this.list[i];
          if(this.$route.params.id == element.id){
             this.list.splice(i,1);
          }else{
              if(element.id == daddress.id){
                     this.list.splice(i,1,daddress);
                     return;
               }
          }
        
     }     
  }
};
</script>




