<template>
    <div class="pcbIndex">
       <div class="title"></div>
       <ul class="flex-row">
           <li class="item" v-for="(item,index) in numArr" @click="selNum(item,index)" :class="index==current ? 'active':''">{{item}}</li>
       </ul>
       <div class="other-num">
          <van-field v-model="digit" type="digit" label="" placeholder="50.500" @change="fillNum(digit)"/> 
       </div>
       <div class="op-box flex-row">
           <div class="left-quit flex-1 cl-f90" @click="quit()"></div>
           <div class="right-submit flex-1" @click="submit()"></div>
       </div>
    </div>
</template>
   
<script>
   import Vue from "vue"
    export default {
        name:'.pcbIndex',
        data(){
            return {
              numArr: [
                  5,10,15,20,25,30,50,70,100,125,150,200,250,300,350,400,450,500,600
              ],
              sub_num:0,
              current:-1,
              digit:''
            }
        },
        methods:{
            selNum(item,index){
              this.sub_num = item;
              this.current = index;
            },
            fillNum(val){
               if(val < 500){
                  this.$toast.fail({
                       message:'500'
                  })
                  this.digit = '';
               }else{
                 if(val % 50 == 0 ){
                     this.sub_num = val;
                     this.current = -1;
                 }else{
                      this.$toast({
                        message:'50'
                     })
                 }   
               }
            },
            submit(){
               if(this.sub_num <= 0 ){
                  this.$toast.fail({
                       message:''
                  })
               }else{
                  
               }
            },
            quit(){
               this.sub_num = '';
               this.current = -1;
            }
        }
    }
</script>
<style >
     .pcbIndex  .van-cell{
       border: solid 1px #d1d3d8;
       height: 0.8rem;
       line-height: 0.8rem;
       padding: 0!important;
    }
    .pcbIndex .van-field__label{text-align: center;}
    .pcbIndex .van-field__control{
        padding-left:0.24rem;
        border-left: solid 1px #d1d3d8;
    }
    body,html{background-color: #fff;width:100%;height: 100%;}
</style>
<style scoped>

    .flex-row{
        display:flex;
        flex-direction: row;
        flex-wrap: wrap;
       justify-content: space-between;
    }
    .flex-1{flex:1}
    .pcbIndex .title{
        width: 100%;
        height: 1rem;
        line-height: 1rem;
        font-size: 0.34rem;
        padding-left:0.3rem;
        box-sizing: border-box;
        border: solid 1px #e3e4e8;
    }
    .other-num{
        width: 100%;
        padding: 0 0.32rem;
        margin-top:0.24rem;
        box-sizing: border-box;
    }
    .pcbIndex ul{
        width:100%;
        padding:0 0.3rem;
        box-sizing: border-box;
    }
    .pcbIndex ul li{
        	width: 22%;
            height:0.62rem;
            background-color: #ffffff;
            border-radius: 4px;
            border: solid 1px #d1d3d8;
            margin-top:0.3rem;   
            text-align: center;
            line-height: 0.62rem;
            font-size: 0.28rem; 
    }
   .op-box{
       	width: 100%;
        height: 1.12rem;
        background-color: #ffffff;
        box-shadow: 0px -3px 5px 0px 
            rgba(0, 0, 0, 0.12);
         position: fixed;
         bottom:0px;
         left:0;
         letter-spacing: 1px;
         text-align: center;
         font-size: 0.34rem;
         line-height: 1.12rem;
   }
   .right-submit{
        height: 100%;
        background-color: #ff9900;
        color:#fff;
        text-align: center;  
   }
   .active{
       background-color: #f90!important;
       color:#fff;
   }
</style>


