<template>
    <div class="sellabel">
        <HomePageTop :TopTitle="TopTotle" class="bg_fff"></HomePageTop>
        <ul class="flex-row mt24">
            <li v-for="(item,index) in labelArr" class="item" 
            :class="current==index || item.isSel==true?'active':''" :key="index" @click="sel(item,index)">{{item.name}}</li>
        </ul>
    </div>
</template>
<script>
import HomePageTop from "./common/HomePageTop"
export default {
    name: '.sellabel',
    components: {HomePageTop},
    data () {
        return {
            TopTotle:{name:"",left:true},
            current:-1,
            show:false,  
            labelArr:[
                {
                    name:'',
                    id:'1',
                    isSel:false
                 },
                   {
                    name:'/',
                    id:'1',
                    isSel:false
                 },
                  {
                    name:'VR/AR/',
                    id:'1',
                    isSel:false
                 },
                   {
                    name:'',
                    id:'2',
                    isSel:false
                 },
                   {
                    name:'',
                    id:'1',
                    isSel:false
                 },
                   {
                    name:'',
                    id:'1',
                    isSel:false
                 }
            ]
        }
    },
    methods:{
        sel(item,index){
           this.current = index;
           this.show = true;
              this.$dialog.confirm({
                // showCancelButton:true,
                width:'100%',
                className:'dialogbtn',
                confirmButtonText: "", //
                cancelButtonText: "" //
                    }).then(()=> {
                        item.isSel = true;
                    }).catch(() => {
                       this.current = -1;
                       item.isSel = false;
                })
        }
    }
}
</script>
<style >
   body,html{background-color: #fff!important;}
   .dialogbtn{
       border-radius: 0px!important;
       bottom:-24px;
       top:auto;
   }
   .dialogbtn .van-dialog__cancel{
       color:#333;
       font-size: 0.35rem;
       height: 0.96rem;;
   }
    .van-dialog__confirm, .van-dialog__confirm:active{
       background-color: #f90!important;
       color:#fff;
       font-size: 0.35rem;
       height: 0.96rem;;
   }
</style>
<style scoped>
.sellabel ul{
    padding:0 0.3rem;
    box-sizing: border-box;
    justify-content: start;
}
   .item{
      	height: 0.72rem;
        background-color: #f4f4f4;
        border-radius: 4px;
        line-height: 0.72rem;
        font-size: 0.28rem;
        width:auto;
        padding:0 0.2rem;
        box-sizing: border-box;
        margin-right:0.26rem;
        margin-bottom:0.26rem;   
        border: solid 1px #f4f4f4;
   }
   .active{
       background-color: #fffbf3;border: solid 1px #ff9900!important;
   }
</style>


